/* News feed */

.newsfeed-wrap {
  max-height: 80vh;
  margin: 0;
  padding: 0;
  transition: scroll 0.5s;
  scroll-behavior: smooth;
  overscroll-behavior: none;
}

.newsfeed {
  min-width: 25em;
  margin: 0.5rem 0.3rem 2rem 0;
  padding: 0;
  list-style-type: none;
  overscroll-behavior: none;
}
